import { defineComponent, reactive, ref, watch } from 'vue';
import { useStore } from 'vuex';
import BootStrap from './bootstraps/bootstrap.vue';
import { ElLoading, ElNotification } from 'element-plus';
import localCache from '@/utils/cache/cache';
import { bootstrapsForm } from '../config/node-config';
export default defineComponent({
  components: {
    BootStrap
  },

  setup() {
    const store = useStore();
    const dialogFormVisible = ref(false);
    let form = reactive(localCache.getCache('WASM_BOOTSTRAP_STORAGE_KEY') ?? bootstrapsForm);

    const recoverForm = () => {
      if (localCache.getCache('WASM_BOOTSTRAP_STORAGE_KEY')) {
        localCache.deleteCache('WASM_BOOTSTRAP_STORAGE_KEY');
      }

      form.bootstrap = '';
      form.bootstraps = ['/ip4/94.23.17.189/tcp/10667/ws/p2p/16Uiu2HAmGTcDnhj3KVQUwVx8SGLyKBXQwfAxNayJdEwfsnUYKK4u', '/ip4/139.155.182.182/tcp/33333/ws/p2p/16Uiu2HAmBUxzcXjCydQTcKgpXvmBZc3paQdTT5j8RXp23M7avi1z'];
      localCache.setCache('WASM_BOOTSTRAP_STORAGE_KEY', {
        bootstraps: ['/ip4/94.23.17.189/tcp/10667/ws/p2p/16Uiu2HAmGTcDnhj3KVQUwVx8SGLyKBXQwfAxNayJdEwfsnUYKK4u', '/ip4/139.155.182.182/tcp/33333/ws/p2p/16Uiu2HAmBUxzcXjCydQTcKgpXvmBZc3paQdTT5j8RXp23M7avi1z']
      });
    };

    const addBootstrap = () => {
      if (form.bootstrap != '') {
        form.bootstraps.push(form.bootstrap);
        form.bootstrap = '';
      } else {
        ElNotification({
          title: 'Info',
          message: 'bootstrap',
          type: 'info'
        });
      }
    };

    const deleteBootstrap = index => {
      form.bootstraps = form.bootstraps.filter((item, i) => {
        if (i != index) {
          return item;
        }
      });
    };

    const loginAction = () => {
      if (localCache.getCache('WASM_BOOTSTRAP_STORAGE_KEY')) {
        localCache.deleteCache('WASM_BOOTSTRAP_STORAGE_KEY');
      }

      localCache.setCache('WASM_BOOTSTRAP_STORAGE_KEY', form.bootstraps);
      const loading = ElLoading.service({
        lock: true,
        text: '...',
        background: 'rgba(0, 0, 0, 0.5)'
      });
      store.dispatch('login/nodeLoginAction', { ...form
      });
      loading.close();
    };

    watch(form, (oldValue, newValue) => {
      if (newValue.bootstraps.length == 0) {
        ElNotification({
          title: 'Info',
          message: 'bootstraps',
          type: 'info'
        });
      }
    });
    return {
      form,
      dialogFormVisible,
      addBootstrap,
      deleteBootstrap,
      recoverForm,
      loginAction
    };
  }

});